import cuPanel from '@/components/custom/cu-panel.vue';
import cuCell from '@/components/custom/cu-cell.vue';
import cuCellGroup from '@/components/custom/cu-cell-group.vue';
import uniList from '@/components/uni-list/uni-list.vue';
import uniListItem from '@/components/uni-list-item/uni-list-item.vue';
import { tokenget,tokenpost } from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		uniList,
		uniListItem
	},
	data() {
		return {
			loading: false,
			reqData: {
				cashaccountname: '',
				cashaccountno: '',
				cashaccounttype: '',
				amount: '',
				isdelete: 0
			},
			accountid: 0,
			isdeleteDict: ['正常','禁用'],
			AccountTypeDict: ['', '银行账号', '微信', '支付宝', '现金'],
			title: '账户查看'
		};
	},
	onShow() {},
	onLoad: function(option) {
		this.accountid = option.id;
		this.loadData();
	},
	methods: {
		handleAccountTypeChange(val) {
			this.reqData.cashaccounttype = val.detail.value;
		},
		handleForbiddenChanage(val) {
			this.reqData.isdelete = val.detail.value;
		},
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			});
		},
		loadData() {
			tokenget(api.MyCashAccountGet, this.accountid)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.reqData = res.data.data;
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		}
	}
};
